export class CallbackItem {
    constructor(_proc, _id = -1) {
        this._proc = _proc;
        this._id = _id;
        this._caller = null;
    }
    get id() {
        return this._id;
    }
    call() {
        if (!this._proc)
            new Error('Callback function is not set');
        this._proc.call(this._caller);
    }
}
