export class CursorPositionCalculator {
    constructor() {
        this._element = null;
    }
    setElement(element) {
        this._element = element;
    }
    getMouseCursorPosition(pageX, pageY) {
        if (!this._element)
            return { x: 0, y: 0 };
        const rect = this._element.getBoundingClientRect();
        return {
            x: (pageX - window.scrollX - rect.x),
            y: (pageY - window.scrollY - rect.y)
        };
    }
}
