export class Injector {
    inject(calledObject, method, before, after) {
        const original = calledObject[method.name];
        calledObject[method.name] = (...args) => {
            if (before)
                before();
            const result = original.apply(calledObject, args);
            if (after)
                after();
            return result;
        };
    }
}
